import { supabase } from "@/lib/supabase";
import { notFound } from "next/navigation";
import StoreFront from "@/components/StoreFront";

// define the type for the props
interface PageProps {
  params: Promise<{ slug: string }>;
}

export default async function StorePage({ params }: PageProps) {
  
  // 1. AWAIT THE PARAMS (Crucial Fix for Next.js 15+)
  const resolvedParams = await params;
  const slug = resolvedParams.slug;

  // 2. Fetch the Store using the resolved slug
  const { data: store } = await supabase
    .from("stores")
    .select("*")
    .eq("slug", slug)
    .single();

  // If store doesn't exist, show 404
  if (!store) {
    return notFound();
  }

  // 3. Fetch Products for this Store
  const { data: products } = await supabase
    .from("products")
    .select("*")
    .eq("store_id", store.id)
    .eq("is_active", true);

  // 4. Fetch Categories for this Store
  const { data: categories } = await supabase
    .from("categories")
    .select("id, name")
    .eq("store_id", store.id);

  // 5. Pass everything to the Client Component
  return (
    <StoreFront 
      store={store} 
      products={products || []} 
      categories={categories || []} 
    />
  );
}